.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 87px;
  padding: 13px 24px; }

@media screen and (min-width: 768px) {
  .header {
    padding: 15px 5px; } }

@media screen and (min-width: 1366px) {
  .header {
    padding: 30px 100px; } }

.nav--hidden {
  display: none; }

@media screen and (min-width: 768px) {
  .nav--hidden {
    display: unset; }
  .nav__items {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    gap: 20px; }
  .nav__link {
    outline: none;
    text-decoration: none;
    font-size: 12px;
    color: #1a1a1a;
    text-transform: uppercase; } }

.promo {
  display: flex;
  flex-direction: column;
  padding: 30px 15px 0px 15px;
  width: 100%;
  min-height: 400px;
  object-fit: contain;
  background: url(./static/banner-mobile.png) no-repeat; }
  .promo__title {
    display: block;
    padding: 5px 20px;
    margin: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 26px;
    color: white;
    text-transform: uppercase;
    background-color: #1a1a1a; }
  .promo__description--hidden {
    display: none; }
  .promo__info {
    padding: 5px 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: white; }
  .promo__data, .promo__place {
    margin: 0;
    color: #ea4646; }
  .promo__data {
    font-size: 16px;
    font-weight: 700; }
  .promo__place {
    font-size: 12px; }

@media screen and (min-width: 361px) {
  .promo {
    background: url(./static/banner-desktop.png) no-repeat; }
    .promo__title {
      font-size: 30px; } }

@media screen and (min-width: 768px) {
  .promo {
    background-image: url(./static/banner-table.png) no-repeat; }
    .promo__title {
      font-size: 38px; }
    .promo__description {
      display: block;
      padding: 5px 20px;
      margin: 5px 0px;
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
      font-size: 16px;
      color: white;
      background-color: #1a1a1a; } }

@media screen and (min-width: 1366px) {
  .promo__title {
    font-size: 38px; }
  .promo__description {
    font-size: 20px; }
  .promo__data {
    font-size: 20px;
    font-weight: 700; }
  .promo__place {
    font-size: 14px; } }

.btn-logo {
  border: none;
  background-color: inherit; }

.btn-club--hidden {
  display: none; }

@media screen and (min-width: 768px) {
  .btn-club {
    border: none;
    background-color: inherit; }
    .btn-club--hidden {
      display: unset; } }

.btn-red {
  border: none;
  background-color: inherit;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 30px;
  margin-left: 8px;
  margin-top: 25px;
  color: white;
  background-color: #ea4646;
  text-transform: uppercase;
  font-size: 12px; }

@media screen and (min-width: 768px) {
  .btn-red {
    margin-top: 46px; } }

@media screen and (min-width: 1366px) {
  .btn-red {
    margin-top: 40px; } }

.btn-menu {
  border: none;
  background-color: inherit; }

@media screen and (min-width: 768px) {
  .btn-menu--hidden {
    display: none; } }

*,
*::before,
*::after {
  box-sizing: border-box; }

.page {
  background-color: white; }

.content {
  display: flex;
  flex-direction: column; }
